.App {
  text-align: center;
}

input[type=color] {
  border: none;
}

html {
	height: 100%;
}

body {
	overflow: hidden;
 	height: inherit;
}

.cursor {
	top: 0;
	left: 0;
	width: 16px;
	height: 16px;
	border: 3px solid orange;
	border-radius: 50%;
	position: absolute;
	z-index: 1010;
	pointer-events: none;
	position: fixed;
}

#draw {
	z-index: 100;
	cursor: none;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

